.Title-Projects{
	text-align: center;
	font-size: 44px;
	margin: 70px 0 -65px;
}
.m{
	position: relative;

	transform: rotate(60deg) translateX(40px) scale(20px) skewX(50deg);
	animation: AnimationTitle infinite 10s ease;
}
.y{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .25s;
}
.p{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .4s;
}
.r{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .5s;
}
.o{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .6s;
}
.j{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .7s;
}
.e{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .8s;
}
.c{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: .9s;
}
.t{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: 1s;
}
.s{
	animation: AnimationTitle infinite 10s ease ;
	animation-delay: 1.1s;
}
@keyframes AnimationTitle {
	0%{color: rgb(255, 255, 255);
		text-shadow: .2px .2px 0px rgb(0, 0, 0);opacity: 0;}
	20%{color: rgb(255, 255, 255);
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;opacity: 1;}
	40%{color: rgb(173, 190, 255);
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;opacity: 1;}
	60%{color: rgb(173, 190, 255);
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;opacity: 1;}
	80%{color: rgb(173, 190, 255);
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;opacity: .5;transform: perspective(600) scaleZ(-60px);}
	85%{color: rgb(173, 190, 255);
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;opacity: .5;}
	100%{color: #444;
		text-shadow: .2px .2px .2px #000,.2px .2px .2px #000;
	opacity: 0;}
}

.container{
	max-width: 900px;
	margin:70px auto;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	padding: 20px 0px;
	z-index: 10;
	gap: 20px;
}
.container a{
	position: relative;
}
.container_card{
	height: 385px;
    width: 260px;
    z-index: 1000;
    position: relative;
    display: flex;
}
.container a {
	transform: scale(0) perspective(600px);
	animation: zoomCards 1s  cubic-bezier(.05,-0.01,.27,1.55);
	animation-fill-mode: forwards;
}
.container a:nth-child(1){
	animation-delay: 1s;
}
.container a:nth-child(2){
	animation-delay: 1.8s;
}
.container a:nth-child(3){
	animation-delay: 2.6s;
}
.container a:nth-child(4){
	animation-delay: 3.6s;
}

@keyframes zoomCards {
	0%{transform: scale(0) perspective(600px)}
	100%{transform: scale(1) perspective(600px);}
}

.container_card div {
	display: inline-block;
    overflow: hidden;
    height: 100%;
    position: relative;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container_card:hover figure{
	transform: perspective(600px) rotateY(180deg);
}

.container_card:hover figure img{
	filter: blur(2px);
}

.container_card:hover figure .trasera{
    opacity: 1;
}

figure{
	width: 250px;
	height: 300px;
	margin: 0;
	position: relative;
	transition: all ease .7s;
	transform-style: preserve-3d;
	transform: perspective(600px) rotateY(0deg);
    border-radius: 9px;
}

figure .frontal, figure .trasera{
	height: 100%;
	border-radius: 9px;
	transition: all ease .7s
}

figure .frontal{
	display: block;
	width: 250px;
	height: 300px;
}

figure .trasera{
	position: absolute;
	top: 0;
	color: white;
	transform: perspective(600px) rotateY(180deg);
    opacity: 0;
	overflow: auto;
	text-align: center;
	background: #38383855;
	background: rgba(51, 51, 51, 0.274);
	transition: all ease .7s;
	background-color: rgba(37, 35, 35, 0.876);
}

.trasera h2{
	background: rgb(93, 73, 73);
	text-align: center;
	line-height: 2;
	font-size: 27px;
	margin-bottom: 15px;
	margin-top: 15px;
	color: rgba(255, 255, 255, 0.911);
	text-shadow: .1px .1px 1px #000,.1px .1px 1px #000,-.1px -.1px 1px #000
}
.trasera hr{
	width: 90%;
	margin: auto;
	color: #aaa
}
.trasera p{
	margin-top: 10px;
	text-align: center;
	font-size: 22px;
	padding: 10px;
	color: rgb(200, 200, 200);
    font-weight: 700;
	letter-spacing: 1px;
	line-height: 1;
	font-weight: 100;
}

.container-responsive-cards{
	display:none
}

