		font-size: 31px;
		margin: 30px auto;
	}
	.container {
		display: none;
	}
	.container-responsive-cards{
		position: relative;
		width: 85vw;
		display: block;
		margin: 20px auto;
		max-width: 353px;
		min-width: 320px;
	}
	.container_card-res{
		position: relative;
		width: 100%;
		display: flex;
		gap: 10px;
		background: #252534a6;
		padding: 15px;
		box-sizing: border-box;
		text-align: center;
		border-radius: 255px 0px 100px;
		outline: 1px solid #252534;
