*{
    margin: 0;
    padding: 0;
    /* font-family: geneba; */
}

.header{
    width: 100%;
    height: 60px;
    background: rgba(38, 38, 53, 0.941);
    background: rgb(26, 26, 30);

    color: rgb(126, 61, 61);
    position: relative;
}

.header-contenedor{
    position: relative;
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: space-between;

    /* background-color: red; */
}
.header-contenedor-logo{
    width: 35%;
    max-width: 300px;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

    /* background-color: aquamarine; */
}
.header-contenedor-logo h3{
    color: rgb(255, 174, 174);
    font-size: 23px;
    display:block;
    /* padding: 2px; */
}
.header-contenedor-logo p{
    color: rgb(216, 144, 144);
    font-size: 15px;
    display: block;
    margin: 0;
}

.header-contenedor-menu{
    width: 60%;
    max-width: 500px;

    /* background-color: brown; */
}
.header-contenedor-menu .header-contenedor-menu-lista{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.header-contenedor-menu .header-contenedor-menu-lista li{
    list-style: none;
    padding: 5px;
    flex-grow: 1;
    text-align: center;
}
.header-contenedor-menu-lista li .a{
    text-decoration: none;
    padding: 13px 20px;
    font-size: 13px;
    letter-spacing: .6px;
    /* width: 100%; */
    color: rgb(255, 174, 174);
    border-radius: 5px;
    display: inline-block;
}
.header-contenedor-menu-lista li .a:hover{
    background:linear-gradient(to bottom, rgb(102, 99, 99), rgb(134, 102, 102) );
    color: rgba(255, 255, 255, 0.938);
}
.header-contenedor-menu-lista li .a:active{
    background:linear-gradient(to bottom, rgba(0, 0, 0, 0.164), rgba(0, 0, 0, 0.329) );
}


.contenedor-responsive{
    display: none;
}

@media screen and (max-width: 720px){
    /* header{
        position: fixed;
    } */
    .header-contenedor{
        display: none;
    }
    .contenedor-responsive{
        display: inline-block;
        background-color: rgba(38, 38, 53, 0.941);
        background: rgb(26, 26, 30);
        width: 100vw;
        min-width: 250px;
        position: absolute;
        z-index: 1000;
        height: 60px;
        /* display: none; */
    }

    .cont-resp-flex{
        display: flex;
        background-color: rgba(38, 38, 53, 0.941);
        background: rgb(26, 26, 30);

        justify-content: space-between;
        align-items: center;
        height: 100%;
        padding: 5px;
        box-sizing: border-box;
    }
    .cont-resp-flex-logo{
        display: inline;
        color:rgb(255, 174, 174);
        /* background-color: blue; */
    }
    .cont-icon-nav{
        width: 20px;
        height: 30px;
        /* background-color: blue; */
        display: flex;
        justify-content: center;
        margin-right: 14px;
        color: #fffd;
        cursor: pointer;
        /* outline: 2px solid red; */
padding: 5px 10px;




    }



    .cont-nav-resp{
        width: 100%;
        position: relative;
        height: 111vh;
        background-color: rgba(0, 0, 0, 0);
        left: -1000px;
        transition: left 1s cubic-bezier(.39,.58,.57,1);
        transition: background-color 1s cubic-bezier(1,1,.45,.86);
        /* transition: transform 1s cubic-bezier(1,1,.45,.86); */


    }
    @keyframes myAnimacion {
        0%{background-color: rgba(0, 0, 0, 0.644)}
        25%{background-color: rgba(0, 0, 0, 0.483)}
        50%{background-color: rgba(0, 0, 0, 0.322)}
        75%{background-color: rgba(0, 0, 0, 0.161)}
        100%{background-color: rgba(0, 0, 0, 0)}

    }
    .cont-nav-resp-ul{
        height: 128vw;
        height: 111vh;
        display: flex;
        width: 68%;
        flex-direction: column;
        font-size: 20px;
        left: -500px;
        align-items: center;
        position: absolute;
        transition: left 1s cubic-bezier(.39,.58,.57,1);
        background-color: rgb(175, 177, 194);
    }
    .mostrarFondo{
        left: 0px;
        background-color: #000000a4;
        /* position:fixed; */
    }
    .mostrarMenu{
        left: 0px;
    }





    .svg-inline--fa{
        width: 16px;
        margin-right: 9px;
    }

    .cont-nav-resp-ul li{
        list-style: none;
        display: block;
        width: 100%;
        text-align: center;

    }
    .cont-nav-resp-ul li a{
        color: #000;
        padding: 20px 0px;
        background-color: rgb(175, 177, 194);
        display: block;
        width: 100%;
        text-decoration: none;
    }
    .cont-nav-resp-ul li a:active{
        background-color: rgb(161, 161, 139);
    }


}

@media screen and (max-width: 380px){

    /* esto de abajo es solo de prueba, tengo que borrarlo o modificarlo */
    /* header{
        position: fixed;
    } */
    .cont-nav-resp-ul{
        width: 100%;
    }
}

