.contenedorSaludo{
    background:rgb(255, 255, 255);
    width:550px;
    /* height:320px; */
    position:fixed; 
    top:100px;
    padding:50px;
    margin:0 auto;
    outline: 2px solid rgb(0, 0, 0);
    border-radius: 3px;
}
.contenedorSaludo::before{
    content: "";
    background-color: rgb(0, 0, 0);
    width: 100vw;
    opacity: .4;
    height: 120vh;
    position: fixed;
    left: 0;
    top: 0;
  }
.contenedorSaludo p{
    font-size:23px;
    line-height:1.5;
    font-weight: 450;
}
.contenedorSaludo span{
    position:absolute;
    display:inline-block;
    top:0;
    right:0;
    padding:20px;
    font-size:20px;
    cursor:pointer
}

@media screen and (max-width:500px) {
    .contenedorSaludo{
        width: 65vw;
        z-index: 50000;
        padding: 40px;
    }
    .contenedorSaludo p{
        font-size: 18px;
    }
}