.contenedor-pizarra{
    background: rgb(116, 52, 52);
    width: 1000px;
    height: 500px;
    margin: 0px auto;
    display: flex;
    justify-content: space-evenly;
    padding: 20px 0px;
    /* text-align: center; */
    /* color: rgb(31, 31, 47); */
}
.parteIzquierda{
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 30%;
    opacity: .5;
    transition: opacity .5s ;
}
.parteIzquierda:hover{
    opacity: 1;
}

.paletaColoresSombra{
    display: flex;
    max-width: 150px;
    flex-wrap: wrap;
    gap: 10px;
    background: rgb(73, 59, 59);
    justify-content: space-evenly;
    padding: 20px;
    border-radius: 20px;
    outline: 2px solid rgb(131, 121, 121);
    opacity: .9;
    transition: opacity .5s ease-out;
    margin-top: -10px;
    position: relative;
}
.colorSombra{
    outline: 1px solid rgb(0, 0, 0);
    width: 25px;
    height: 25px;
    border-radius:100% 100% 100% 0px;
    transform: rotateZ(136deg) ;
    cursor: pointer;
    margin: 2px 0px;
}
#range{
    cursor: pointer;
}
#canvas{
    background: #fff;
    width: 500px;
    height: 500px;
}
.paletaColores{
    display: flex;
    max-width: 170px;
    min-width: 150px;
    flex-wrap: wrap;
    gap: 10px;
    background: rgb(73, 59, 59);
    justify-content: space-evenly;
    padding: 20px;
    outline: 2px solid rgb(131, 121, 121);
    opacity: .5;
    transition: opacity .5s ease-out;
    margin: 20px;
    border-radius: 20px;
}
.paletaColores:hover,.coloresSombra:hover{
    opacity: 1;
}

.color{
    outline: 1px solid rgb(0, 0, 0);
    text-shadow: 5px 5px 5px red;
    width: 38px;
    height: 38px;
    cursor: pointer;
    border-radius:100% 100% 100% 0px;
    transform: rotateZ(136deg) ;
}



@media screen and (max-width:500px) {
    #sombra , #labelSombra, #rangeSombra,#rangeLinea, .ranges,#canvas,.paletaColores,.color,.paletaColoresSombra{
        display: none;
    }
.contenedor-pizarra{
    width: 100vw;
    background-color: white;
    height: 200px;
    text-align: ce;
}
    .contenedor-pizarra::before{
        content: "Sorry, your browser not support the tag 'canvas'. for more information visit the website: https://developer.mozilla.org/en-US/docs/Web/HTML/Element/canvas";
        width: 250px;
        background-color: rgb(179, 152, 152);
        top: 170px;
        position: absolute;
        padding: 50px 50px;
    }
}