@media screen and (max-width: 280px){
	/* body{
	background: #42424244
	} */

	.contenedor{
		margin: 0;
		padding:0;
		width: 100%;
		max-height: 450px;
		box-shadow: none
	}
	.screen{
		font-size: 30px
	}
	.btns{
		margin:6px;
	}

	.btns a{
		font-size: 32px;
		padding: 12px 11px 14px 19px;
		border: 1px solid black;
		width: 30px;
		height: 40px;
		box-shadow: none;
		text-shadow: none
	}
	.btns a:hover{
		box-shadow: none
	}
}