.contenedor-producto-lista{
    background: none;
    margin: 10px 10px 10px 10px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 4px;
    box-shadow:0px 0px 2px rgb(0, 0, 0);

}
.contenedor-producto-lista table{
    background-color: rgb(216, 195, 195);
}

.contenedor-producto-lista td{
    border:0;
    box-shadow:1px 1px 1px  #000;
    min-width: 122px;
    max-width: 122px;
}
.contenedor-producto-lista th{
    border:0;
    box-shadow:1px 1px 1px  #000
}
.thBtn{
    background:#d8c3c3;
    position:relative;
    top:18px;
    border-radius:50%;
    width:45px;
    text-align:center;
    padding:0;
    overflow:hidden;
    background: none;
    border: none;
    box-shadow: none!important;
    cursor: pointer;
}


@media screen and (max-width: 500px) {
    .contenedor-producto-lista{
        margin: 8px 2px;
    }
    .contenedor-producto-lista td{
        min-width: 90px;
        max-width: 90px;
    }
}