.contenedor-grid{
	display: grid;
	grid-template-columns:repeat(auto-fill, 210px); 
	grid-template-rows: repeat(auto-fill, 1fr); 
	margin:35px 15px 150px 15px;
	grid-auto-flow:dense;
	justify-items:center;
	justify-content:center;
	grid-row-gap: 20px;
	grid-column-gap: 27px;
}

.imgs{
	height: 150px;
	width: 210px;
	border-radius: 3px;
	cursor: pointer;
	border: 1px solid rgb(0, 0, 0)
}
.imgs:nth-child(11){
	width:100%;
	grid-column: 1 / span 2
}

.imgs:nth-child(12){
	width:100% ;
	grid-column: 1 / span 2;
	grid-row: 1 / span 1
}

#mostrador{
	position: absolute;
	width: 100vw;
	height: 101%;
	text-align: center;
	background: rgba(0,0,0,.8);
	top: -1px;
	padding-top: 10px;
	position: fixed;
	display: flex;
	justify-content: center;
	z-index: 1000000;
}

@keyframes lentoC{
	0%{opacity: 0;
	display: block;}
	100%{opacity: 1;
	}
}

@keyframes lentoQ{
	0%{opacity: 1;}
	100%{opacity: 0; display: none}
}

.imgmost{
	height: 96%;
	position: relative;
	z-index: 200;
	margin: auto;
	position: absolute;
	left: 0;
	right: 0;
}

#mostrador p{
	font-size: 22px;
	cursor: pointer;
    background: linear-gradient(to left,rgb(50, 48, 54),rgba(0, 0, 0, 0.575));
    position: absolute;
    display: inline-block;
    z-index: 300;
    padding: 6px 15px 7px 9px;
    top: 0px;
    margin-bottom: 0;
	color: rgb(158, 189, 178);
    margin: 0;
    right: -2px;
    border:1px solid #aaa;
    border-radius: 0px 0px 0px 22px;
}
#mostrador p:hover{
    background: linear-gradient(to left,rgb(48, 54, 54),rgba(68, 68, 68, 0.575));
}






@media screen and (max-width: 420px){
body{
	background: radial-gradient(circle, rgba(19, 10, 24, .2), #5e4239);

	background-repeat: no-repeat;
	background-attachment: fixed;
}
.contenedor-grid{
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	/* grid-template-rows: repeat(auto-fill, 100px); */
	margin: 35px 20px;
	grid-gap: 15px;
}
.imgs{
	height: 100%;
	width: 100%;
	border-radius: 2px;
	font-size: 5px;
	border: .1px solid #000
}

#mostrador{
	width: 100%;
	height: 100vh;
	text-align: center;
	background: rgba(0,0,0,.7);
	top: -1px;
	padding-top: 1px
}
#mostrador p{
	font-size: 10px;
    padding: 5px 7px;
    border:1px solid #aaa;
}

#mostrador p{
	font-size: 15px;
    padding: 6px 10px 7px 9px;
    top: 55px;
    right: 0px;
    border-radius: 22px 0px 0px 22px;
}

.imgmost{
	position: relative;
	height: auto;
	width: 100vw;
	border-radius: 3px;
	margin-top: 30%;
}

.imgs:nth-child(11){
	grid-column: 1 / span 1;
	grid-row: 7 / span 1;
	width: 100%;
	height: 100%;
}
.imgs:nth-child(12){
	width: 100%;
	height: 100%;
	grid-column: 1 / span 1;
	grid-row: 4 / span 1
}
.imgs:nth-child(21){
	grid-column: 1 / span 1;
	grid-row: 9 / span 1;
	width: 100%;
	height: 100%;
}

}