/* estilos por mi */
.contenedor-principal{
  display: flex;
  position: relative;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  font-family: 'Yrsa', serif;
  
}
.contenedor-principal .titulo-tienda{
  padding:50px;
  font-size: 50px;
  text-shadow: 1px 1px 0px rgb(255, 239, 239),2px 2px 1px rgb(0, 0, 0);
  font-family: 'Lucida Sans Unicode';
  font-style:oblique;
  background-color: rgba(41, 41, 41, 0.562);
  margin: 33.5px 0;
}

.contenedor-productos{
  padding: 70px 0;
  background:linear-gradient(to right,#807e7e,#78a478);
  display: flex;
  gap: 50px;
  flex-wrap: wrap;
  justify-content: center;
}

.titulo-tienda::before{
  filter: blur(1px);
  content: "";
  background: url(./images/fondo.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  min-width: 100vw;
  opacity: .9;
  min-height: 1000px;
  max-height: 1000px;
  z-index: -1;
  position: fixed;
  left: 0;
  top:-500px;
}




@media screen and ( max-width:500px) {
  .contenedor-principal .titulo-tienda{
    font-size: 35px;
    padding: 35px;
    margin: 28px 0;
  }
  .titulo-tienda::before{
    width: 150vw;
    height: 500px;
    min-height: 500px;
    top: -180px;
    padding: 0px;
    left: -150px;
  }
  .contenedor-productos{
    gap: 40px;
  }
  
}