.contenedor-btn{
    color:black; 
    background:#2a9e2a;
    padding:0px;
    text-align:center;
    border-radius:50%;
    width:100px;
    height:100px;
    position:fixed;
    bottom:40px;
    right:20px;
    cursor:pointer;
    box-shadow:3px 3px 5px black;
    border:1px solid #255325;
    box-sizing:border-box;
    opacity: .85;
    transition: opacity .2s;
    z-index: 10000;
}
.contenedor-btn img{
    margin-top: 27px;
    width:55px;
}
.contenedor-btn:hover{
    opacity: 1;
}



@media screen and (max-width: 500px) {
    .contenedor-btn{
        width: 55px;
        height: 55px;
    }
    .contenedor-btn img{
        width: 30px;
        margin-top: 16px;
    }
    
}