.contenedor-lista-carrito{
    background: rgb(228, 228, 231);
    min-width: 350px;
    text-align: center;
    position: fixed;
    max-height: 450px;
    align-self: flex-end;
    margin-right: 30px;
    top: 10px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.829);
}

.contenedor-lista-carrito .span{
    display:flex;
    width:100%;
    background:#caa;
    justify-content:space-between;
}
.contenedor-lista-carrito h3{
    /* background-color: rgba(95, 95, 90, 0.582); */
    text-align: left;
    /* padding-left: 20px; */
    /* margin-top: 10px; */
    margin: 15px 0px 15px 15px;
    font-size: 23px;
}
.contenedor-lista-carrito .span img{
    width: 35px;
    height: 35px;
    align-self: center;
    margin-right: 10px;
    border-radius: 50px;
    cursor: pointer;
}

.contenedor-lista-carrito .elemento{
    max-height: 380px;
    overflow: auto;
    background-color: rgb(142, 151, 59);
}

.contenedor-lista-carrito .elemento .totalAPagar{
    text-align:right;
    display:inline-block;
    padding:5px 10px;
    margin-bottom:8px;
    margin-right:-110px;
    background:#d8c3c3;
    border-radius:5px;
    position:relative
}




@media screen and (max-width:500px) {
    .contenedor-lista-carrito{
        min-width: 99vw;
        max-width: 100vw;
        width: 99vw;
        /* padding: 0; */
        margin: 0;
        /* right: 5px; */
    }

    .contenedor-lista-carrito .elemento .totalAPagar{
        margin-right: 150px;
    }
    
}