body::before{
    content: "";
    width: 100vw;
    height: 100%;
    background: rgba(0, 0, 0, 0.548);
    display: flex;
    z-index: -100;
    position: fixed;
}
.info-home{
    display: flex;
    margin: 30px auto;
    align-items: center;
    width: 80%;
    max-width: 1000px;
    padding: 30px;
    position: relative;
    box-sizing: border-box;
}

.info-home h1{
    font-size: 5rem;
    color: rgb(158, 139, 139);
    text-shadow: 1.5px 1.5px 0px rgb(7, 7, 7),-1px -1px 0px white, 1.5px 1.5px 10px rgb(7, 7, 7);
    position: relative;
    padding: 10px;
    display: inline-block;
    left: -50px;
    opacity: 0;
    animation: textoH1 1s ease-in-out;
    animation-delay: 1.2s; 
    /*revisar si cuando se carga la pagina home no se ve la imagen de fondo durante un momento comentalo para ver si es esto lo que lo causa*/
    animation-fill-mode: forwards;
}
@keyframes textoH1 {
    0%{ opacity: 0;}
    100%{ opacity: 1;left: 0px;}
}

@media  screen and (max-width: 720px) {
    .info-home{
        width: 98%;
    }
    .info-home h1{
    font-size: 3.5rem;
    }
}
@media  screen and (max-width: 380px) {
    .info-home h1{
        font-size: 2.5rem;
    }
}
