.title-skill{
    color:rgb(155, 155, 155);
    font-size:44px;
    text-align: center;
    margin-top: -100px;
}
.cont-imgs-skill{
    width:80%; 
    flex-wrap: wrap;
    gap: 20px;
    /* height:500px; */
    display: flex;
    align-self: center;
    justify-content: space-around;
    /* background-color: rgba(255, 0, 0, 0.615); */
}
.cont-imgs-skill .img-skill{
    /* outline: 1px solid black; */
    width:100px;
    height:100px;
    margin: 0px;
    align-items: center;
    align-self: center;
    align-content: center;
    margin: auto;
    position: relative;

}

.cont-imgs-skill .img-skill[data-descr]:hover::after{
    background-color: red;
    content: attr(data-descr);
    position: absolute;
    left: 100px;
    top: 54px;
    min-width: 200px;
    max-width: 300px;
    border: 1px #383838 solid;
    border-radius: 10px;
    background-color: #9a9ab0;
    padding: 12px;
    color: #000000;
    font-size: 14px;
    z-index: 1;
}

.img-skill:nth-child(1){
    background:url('../Images/html.png');
    background-size:contain;
    background-repeat: no-repeat;

    /* transform: perspective(600) scaleZ(3); */
}
