    margin: 0;
    padding: 0;
} */
.footer{
    position: relative;
    height: 200px;
    background: rgba(36, 36, 49, 1);
    background: rgb(26, 26, 30);

    width: 100%;
    bottom: 0px;
    
    /* position: sticky; */

}
.footer-container-enlaces{
    /* background-color: red; */
    text-align: center;
    height: 50px;
    margin: 5px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 33px;
    margin-bottom: 57px;
}
.footer-container-enlaces a{
    /* background-color: blue; */
    text-align: center;
    display: flex;
    margin:0px 10px;
    position: relative;
    /* height: 55px; */
    padding: 0;
    border-radius: 5px;
}
.footer-container-enlaces a img{
    border-radius: 5px;
}
.footer-container-enlaces a:nth-child(3) img{
    border-radius: 50%;
}
.footer-container-enlaces a:nth-child(4) img{
    border-radius: 15px;
}
.footer-container-enlaces a img{
    position: relative;
    display: inline-block;
    width: 45px;
    height: 100%;
    filter: contrast(80%);
    /* border-radius: 50%; */
}
.footer-container-enlaces a img:hover{
    filter: contrast(100%);
}
.footer p{
    text-align: center;
    line-height: 1.5;
    bottom: 0;
    color:rgb(216, 144, 144);   
}

@media screen and (max-width: 720px){
.footer-container-enlaces a img{
    filter: contrast(100%);
    width: 33px;


}
.footer-container-enlaces a{
    margin:0px 5px;


}
.footer p{
    position: relative;
    bottom: 10px;
}
}