.btns-cantidad{
    color: red;
    display: flex;
    width: auto;
    max-width: 100px;
    margin: 0;
    justify-content: space-between;
    position: relative;
}
.btns-cantidad .boton{
    height:25px;
    width:25px;
    padding: 0px 18px 0 13px;
    text-align: center;
    background-color: #dbb;
    color: rgb(40, 40, 40);
}
.btns-cantidad .boton:hover{
    background-color: #caa;
    outline: thin solid rgb(63, 63, 63);
}
.btns-cantidad .boton:active{
    background-color: rgb(157, 121, 121);
}

.btns-cantidad b{
    margin: 0px;
    margin-left: -10px;
    padding: 3px 5px;
    color: rgb(0, 0, 0);
}