.cont-producto{
    background-color: #3d3d3d;
    width: 236px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    height: 335px;
    overflow: hidden;
    border-radius: 4px;
}

.cont-producto .cont-img {
    min-width: 190px;
    max-width: 200px;
    max-height: 200px;
    min-height: 200px;
    background-color: rgb(255, 255, 255);
    text-align: center;
    overflow: hidden;
    border: 2px rgb(119, 44, 44) solid;
    border-radius: 3px;
}
.cont-producto img{
    object-fit: contain;
    width: 200px;
    height: 200px;
    font-size: 15px;
}

.cont-producto .cont-texto {
    position:relative;
    width:100%;
    background:#d8c3c3;
    margin-top: 5px;
    text-align: center;
}
.cont-texto h3 {
    margin:8px;
    font-size: 20px;
    display: block;
    text-align: left;
    letter-spacing: .5px;
}
 .cont-texto p {
    margin:5px 15px 0px;
    text-align: right;
    font-size: 18px;

}
.cont-texto button{
    height:50px;
    width:90%;
    margin:8px;
    background: #448844;
    color: black;
    font-size: 14px;
}
.cont-texto button:hover{
    background: #3a7a3a;
    /* background: #214921; */
    outline: 1px solid rgb(44, 43, 43);

}



