.contenedor-confirmacion{
    width:460px;
    height:200px;
    background:rgb(255, 255, 255);
    outline: 2px solid rgb(53, 47, 47);
    border-radius:10px;
    position: fixed;
    box-shadow: 0px 0px 10px;
    top: 100px;
    z-index: 2000;
}
.contenedor-confirmacion::after{
    content: "";
    background-color: rgb(0, 0, 0);
    width: 100vw;
    opacity: .4;
    height: 120vh;
    z-index: -10;
    position: fixed;
    /* width: 100vw; */
    left: 0;
    top: 0;
  }
.contenedor-confirmacion p{
    text-align:center;
    font-size:18px;
    margin: 16px;
}
.contenedor-confirmacion th{
    border:1px solid #000;
}
.contenedor-confirmacion td{
    border:1px solid #0009
}
.contenedor-confirmacion div{
    display:flex;
    justify-content:space-evenly;
    /* margin-top:20px; */
    margin-top: 13px;
}
.contenedor-confirmacion div button:first-child{
    background:rgb(43, 24, 65);
    width:140px;
    color:rgb(214, 207, 207);
    height:40px
}
.contenedor-confirmacion div button:first-child:hover{
    background:rgb(38, 21, 58);
    outline: 1px solid rgb(44, 43, 43);


}

.contenedor-confirmacion div button:last-child{
    background:#448844;
    width:140px;
    color:black;
}
.contenedor-confirmacion div button:last-child:hover{
    background: #3a7a3a;
    outline: 1px solid rgb(44, 43, 43);

}




@media screen and (max-width:500px) {
    .contenedor-confirmacion{
        width: 90vw;
    }
    
}
